import React from 'react';
import { Modal, Icon, Button, } from 'react-materialize';

import Map from './Maps'
export default class CallModal extends React.Component {
  constructor(props) {
    super(props);
    this.state = { showWarning: true };
    this.handleToggleClick = this.handleToggleClick.bind(this);
  }
  handleToggleClick() {
    this.setState(state => ({
      showWarning: !state.showWarning
    }));
  }
  render() {
    return (
      <Modal
        actions={[
          <Button flat modal="close" node="button" waves="green">Close</Button>
        ]}
        className="modal-btm"
        bottomSheet={false}
        fixedFooter={false}
        header="Contact"
        id="Modal-0"
        open={false}
        options={{
          dismissible: true,
          endingTop: '10%',
          inDuration: 250,
          onCloseEnd: null,
          onCloseStart: null,
          onOpenEnd: null,
          onOpenStart: null,
          opacity: 0.5,
          outDuration: 250,
          preventScrolling: true,
          startingTop: '4%'
        }}
        trigger={this.props.trigger}
      >
        <p>
          <br/>
          <a className='orange btn btn-block' href="tel://+14356551446" >
            <Icon className='left'>local_phone</Icon>1 (435) 655-1446
        </a>
        </p>
        <p>
          <a className='orange btn btn-block' href="mailTo://info@utahcountertops.com" >
            <Icon className='left'>local_phone</Icon>info@utahcountertops.com
        </a>
        </p><p>
          <Map />
        </p>
      </Modal>
    );
  }
}


