import React from 'react';
import 'materialize-css/dist/css/materialize.min.css';
import './App.css';
import Home from './pages/Home';
import About from './pages/About';
import Review from './pages/Review';
import Residential from './pages/Residential'
import Commercial from './pages/Commercial'
import Gallery from "./pages/Gallery";
import Shower from "./pages/Shower";
import Sink from "./pages/Sink";
// import Test from "./elements/Test";

import { BrowserRouter, Route, Switch, useLocation } from 'react-router-dom';
// import { buildUrl } from 'react-instafeed'
import { SocialIcon } from "react-social-icons";
// import Feed from "react-instagram-authless-feed"
import CallModal from './elements/Contact';
import { Button } from 'react-materialize';


function NoMatch() {
  let location = useLocation();

  return (
    <div>
      <h3>
        No match for <code>{location.pathname}</code>
      </h3>
    </div>
  );
}
export default function App() {
  return (
    <BrowserRouter >
      <main>
        <Switch>
          <Route exact path="/">
            <Home />
          </Route>
          <Route exact path="/review">
            <Review />
          </Route>
          <Route path="/about">
            <About />
          </Route>
          <Route path="/gallery">
            <Gallery />
          </Route>
          {/* <Route path="/Test">
            <Test />
          </Route> */}
          <Route path="/residential">
            <Residential />
          </Route>
          <Route path="/commercial">
            <Commercial />
          </Route>
          <Route path="/shower">
            <Shower />
          </Route>
          <Route path="/sink">
            <Sink />
          </Route>
          <Route path="*">
            <NoMatch />
          </Route>
        </Switch>
        <CallModal trigger={
          <Button className="fixed-action-btn btn-floating btn-large red" node="button">
            <i style={{ fontSize: "2.3rem", paddingLeft: "21%" }} className="large material-icons">local_phone</i>
          </Button>
        } />
      </main>
      <br />
      <Foot />
    </BrowserRouter>
  );
}

function Foot() {
  return (<span>
    <footer className="page-footer grey lighten-4">
      <div
        className='left fixed-action-btn-left'
      >
        <SocialIcon
          bgColor="#ff5a01"
          url="https://instagram.com/pauldowlandtile"
        />
        <SocialIcon
          url="https://www.facebook.com/pauldowlandtile" />
        <SocialIcon
          url="https://twitter.com/pauldowlandtile" />
      </div>

      <div className="footer-copyright grey-text">

        <div className="container fts">
          © 2019-<noscript>2018</noscript><script type="text/javascript">document.write(new Date().getFullYear());</script>2021 Deepturn Ltd, All rights reserved.
              <a style={{ right: "10 !important", }} className="footer-right grey-text text-darken-1" href="https://github.com/LandoLabrum/Deepturn-Ltd/blob/main/LICENSE">MIT License</a>
        </div>
      </div>
    </footer>
  </span>
  );
}
