function ContactForm() {
  let history = useHistory();
  function sendEmail(e) {
    e.preventDefault();

    emailjs.sendForm('service_a2kpjid', 'template_b4rmr1q', e.target, 'user_FNtbnhvNY6tLZSICaVHsY')
      .then((result) => {
        console.log(result.text);
        history.push('/thanks')  // redirect
      }, (error) => {
        console.log(error.text);
      });
  }

  return (

    <form className="contact-form" onSubmit={sendEmail}>
      <Row>
        <Col s={12} m={8} l={4} offset="s4 m2 l1">
          <h3 className="">
            <img
              alt=""
              src={logo}
              width="30%"
            />
          </h3>
        </Col></Row>
      <Row>
        <Col s={12} m={8} offset="m2 l1">
          <Card>
            <h3>Let us know how we did</h3>
            <input type="hidden" name="contact_number" />
            <label>Name</label>
            <input type="text" name="user_name" />
            <label>Email</label>
            <input type="email" name="user_email" />
            <label>Message</label>
            <textarea className="materialize-textarea" name="message" />
            <input className="btn green darken-3" type="submit" value="Send" />
          </Card>
        </Col>
      </Row>
    </form>
  );
}
const routes = [
  {
    path: "/review",
    component: Index
  }, {
    path: "/review-form",
    component: ContactForm
    // component: FormD
  },
  {
    path: "/review-google",
    component: GooglePage
  },
  {
    path: "/review-facebook",
    component: FacebookPage
  },
  {
    path: "/thanks",
    component: Thanks
  },
  {
    path: "/",
    component: Home
  },

];

export default function RouteConfigExample() {
  return (
    <Router>
      <div>
        <Switch>
          {routes.map((route, i) => (
            <RouteWithSubRoutes key={i} {...route} />
          ))}
        </Switch>
      </div>
    </Router>
  );
}

function RouteWithSubRoutes(route) {
  return (
    <Route
      path={route.path}
      render={props => (
        <route.component {...props} routes={route.routes} />
      )}
    />
  );
}


